* {
  font-family: "Bowlby One", cursive;
  color: white;
  -webkit-text-stroke: 1px #ae5d40;
}

h1,
h2 {
  -webkit-text-stroke: 1px #ad5d3f;
  text-align: center;
  margin: 0;
}

body {
  margin: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #bfa08e;
}

html {
  height: 100%;
  margin: 0;
  padding: 0;
}

#root {
  height: 100%;
}
.App {
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  background-image: url("https://i.imgur.com/aYeV0H5.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: absolute;
  height: 615px;
  width: 100%;
  bottom: 0;
}

.button {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 5px;
  display: block;
  border-radius: 10px;
  border: 3.5px solid #5d320b;
  font-size: 25px;
  width: 240px;
  height: 100px;
  -webkit-text-stroke: 0px #ae5d40;
  cursor: pointer;
}

.img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 40px;
  height: 40px;
}

.color-green {
  background-color: #4caf50;
}

.color-green:hover {
  background-color: #429347;
}

.color-red {
  background-color: #dd4748;
}

.color-red:hover {
  background-color: #c74244;
}

.color-brown {
  background-color: #bf7e5b;
}
.color-brown:hover {
  background-color: #976447;
}

.d-inblock {
  display: inline-block;
}

.container-element {
  left: 50%;
  top: 30%;
  transform: translate(-50%, -50%);
  position: absolute;
}

.container-principal {
  height: 45%;
}

.container-button {
  padding: 5px;
}

.header {
  background-color: #59202e;
  display: grid;
  grid-template-areas: "logo nav";
}

.Nav {
  grid-area: nav;
  display: grid;
  grid-template-columns: repeat(4, auto);
  align-items: center;
  justify-items: center;
}

.logo {
  width: 80px;
  height: 80px;
  padding: 5px;
}
